export default {
  getAllProducts() {
    return new Promise( (resolve, reject) => {
      setTimeout(() => {
        // console.log('getAllProducts api');
        if(_products) {
          resolve(_products)
        } else {
          reject('backend fail')
        }

      }, 100)
    })
    
  },

  buyProducts(products, cb, errorCb) {
    setTimeout(() => {
      // simulate random checkout failure.
      (Math.random() > 0.5 || navigator.userAgent.indexOf('DuyDat') > -1)
        ? cb()
        : errorCb()
    }, 100)
  },
  getProduct(id) {
    return new Promise( (resolve, reject) => {
      setTimeout(() => {
        // console.log('getProduct api');
        const product = _products[id]
        if(product) {
          resolve(_products[id])
        } else {
          reject('backend fail')
        }
      }, 100)
    })
  }
}